.popup {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    overflow-y: hidden;
}

.popup-content {
    background-color: #151010;
    margin: 15% auto;
    padding: 20px;
    width: 80%;
    max-width: 600px;
    border-radius: 5px;
    max-height: 90vh;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
    overflow-y: auto;
}

.close {
    color: #f4f4f4;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #808080;
    text-decoration: none;
    cursor: pointer;
}