.book-grid img,
.shows-grid img {
    width: 100px;
    height: auto;
    padding: 10px;  
    transition: transform 0.5s;
}

.book-grid img:hover,
.shows-grid img:hover {
    transform: scale(1.2);
    overflow: none;
}

@media (min-width: 768px) {
    .book-grid img,
    .shows-grid img {
        width: 110px;
    }
}

@media (min-width: 992px) {
    .book-grid img,
    .shows-grid img {
        width: 110px;
    }
}

@media (min-width: 1200px) {
    .book-grid img,
    .shows-grid img {
        width: 120px;
    }
}
